import home from "../images/background.jpg";
import React from "react";
import "./Home.css"; // Import your CSS file

function Home() {
  return (
    <div className="home-container">
      <div className="box">
        <h1 className="heading">Welcome to my Store!</h1>
        <img src={home} alt="Banner" className="banner-image" />
      </div>
    </div>
  );
}

export default Home;
