import React from "react";
import "./animate.css";
import iphone from "../images/iphone.jpg";

function Mobiles() {
  return (
    <div className="mob">
      <div className="chmob">
        <div className="imgdiv animate__animated animate__fadeInLeftBig">
          <img src={iphone} alt="logo" style={{ height: "500px" }} />
        </div>
        <div className="infodev animate__animated animate__fadeInRightBig">
          <h1>iPhone 15 - 256GB</h1>
          <br />
          <hr />
          <hr />
          <br />
          <p>
            Key Features : Super Retina XDR display, 6.1-inch (diagonal)
            all-screen OLED display. Dual 48MP camera system (Ultra Wide and
            Main). A16 Bionic chip, 6-core CPU with 2 performance and 4
            efficiency cores, 5-core GPU,16-core Neural Engine. Rated IP68
            (maximum depth of 6 meters up to 30 minutes) under IEC standard
            60529.
          </p>
          <br />
          <br />
          <hr />
          <hr />
          <br />
          <h1>Price : 1,50,000/-</h1>
          <br />
          <button>Buy Now</button>
        </div>
      </div>
    </div>
  );
}

export default Mobiles;
