import React from "react";
import { flipInY, zoomInDown } from "react-animations";
import Radium, { StyleRoot } from "radium";
import electronics from "../images/electronics.jpg";

const styles = {
  zoomIn: {
    animation: "x 1s",
    animationName: Radium.keyframes(flipInY, "zoomIn"),
  },

  zoomInDown: {
    animation: "x 1s",
    animationName: Radium.keyframes(zoomInDown, "zoomInDown"),
  },
};

function Electronics() {
  return (
    <div
      className="mob"
      style={{ marginTop: "100px", backgroundColor: "white" }}
    >
      <div className="chmob">
        <StyleRoot>
          <div className="imgdiv" style={styles.zoomIn}>
            <img
              src={electronics}
              alt="logo"
              style={{ height: "400px", marginTop: "100px", padding: "20px" }}
            />
          </div>
        </StyleRoot>
        <StyleRoot>
          <div className="infodev" style={styles.zoomInDown}>
            <h1>Explore Our Cutting-Edge Electronics Collection</h1>
            <hr />
            <hr />
            <br />
            <p>
              Discover a world of innovation and convenience as you dive into
              our wide-ranging electronics selection. From state-of-the-art
              gadgets to smart solutions for everyday life, our store is your
              gateway to the future of technology. Elevate your lifestyle with
              the latest in electronics and redefine the way you live, work, and
              play.
            </p>
            <br />
            <hr />
            <hr />
            <h1>Affordable Prices</h1>
            <br />
            <button>Buy Now</button>
          </div>
        </StyleRoot>
      </div>
    </div>
  );
}

export default Electronics;
