import React from "react";
import { zoomIn, zoomInDown } from "react-animations";
import Radium, { StyleRoot } from "radium";
import fashion from "../images/fashion.jpg";

const styles = {
  zoomIn: {
    animation: "x 1s",
    animationName: Radium.keyframes(zoomIn, "zoomIn"),
  },

  zoomInDown: {
    animation: "x 1s",
    animationName: Radium.keyframes(zoomInDown, "zoomInDown"),
  },
};

function Fashion() {
  return (
    <div
      className="mob"
      style={{ marginTop: "100px", backgroundColor: "white" }}
    >
      <div className="chmob">
        <StyleRoot>
          <div className="imgdiv" style={styles.zoomIn}>
            <img
              src={fashion}
              alt="logo"
              style={{ height: "400px", marginTop: "100px", padding: "20px" }}
            />
          </div>
        </StyleRoot>
        <StyleRoot>
          <div className="infodev" style={styles.zoomInDown}>
            <h1>Get Branded Clothes Now on One Click!</h1>
            <hr />
            <hr />
            <br />
            <p>
              Elevate your fashion game with our curated collection of top-tier
              branded clothing. Discover the latest trends and timeless
              classics, all at your fingertips. Shopping for quality, style, and
              comfort has never been this convenient. Embrace effortless
              elegance and upgrade your wardrobe with just one click.
            </p>
            <br />
            <hr />
            <hr />
            <h1>Affordable Prices</h1>
            <br />
            <button>Buy Now</button>
          </div>
        </StyleRoot>
      </div>
    </div>
  );
}

export default Fashion;
